






























































































































































import Vue from 'vue';
import Component from 'vue-class-component';
import { Model } from 'vue-property-decorator';

@Component
export default class Avator extends Vue {
    @Model('change') public avatar!: string;

    public url: string = ''; // 选择的图片
    public visible: boolean = false;
    public changeUpload: ((file: any) => void) | null = null; // 选择图片后回调

    // 预览图父div大小
    public previewBox: { width: string; height: string } = {
        width: '1024px',
        height: '1024px'
    };
    public preview: any = null; // 裁剪预览
    public imgIndex: number = 0;

    public created() {
        this.changeUpload = (file: any) => {
            if (
                file.raw.type !== 'image/png' &&
                file.raw.type !== 'image/jpeg'
            ) {
                return this.$message.error('只能上传jpg/png文件!');
            }

            if (file.size / 1024 / 1024 > 5) {
                return this.$message.error('上传文件大小不能超过 5MB!');
            }

            const reader = new FileReader();
            reader.onload = (e: ProgressEvent) => {
                // target.result 该属性表示目标对象的DataURL
                this.url = <string>(<FileReader>e.target).result;
            };
            // 传入一个参数对象即可得到基于该参数对象的文本内容
            reader.readAsDataURL(file.raw);
        };

        this.preview = {
            url: this.avatar || '/images/true.png',
            div: this.previewBox,
            w: 1024
        };

        if (this.avatar) {
            this.imgIndex = 2;
        }
    }

    public realTime(data: any) {
        this.preview = data;

        this.previewBox = {
            width: data.w + 'px',
            height: data.h + 'px'
        };
    }

    public ok() {
        const cropper: any = this.$refs.cropper;
        if (cropper) {
            cropper.getCropData((data: string) => {
                this.$emit('change', data);
                this.$message.success('设置成功');

                this.imgIndex = 2;
                this.visible = false;
            });
        } else {
            this.$message.warning('请选择图片!!!');
        }
    }

    public selectAvator(e: Event) {
        const index = (<HTMLImageElement>e.target).dataset.index;
        if (index) {
            if (+index < 2) {
                this.imgIndex = +index;
            } else if (+index === 2) {
                if (this.avatar) {
                    this.imgIndex = 2;
                }

                this.visible = true;
            }
        }
    }

    public getImgUrl(name: string) {
        const url = ['/images/P.png', '/images/item.png', this.avatar][
            this.imgIndex
        ];

        const tagPhoto = this.getcanvas(url, name).then(c =>
            this.$http.post('/api/tag/upload/tagPhoto', {
                tagPhoto: c.toDataURL('image/png', 1.0)
            })
        );

        let avatar = Promise.resolve(<ResponseData>{
            code: 200,
            message: '',
            pagedData: {
                currentPage: 1,
                datas: [],
                pageSize: 0,
                totalCount: 0
            },
            success: true,
            resultMap: {
                avatarUrl: url
            }
        });
        if (this.imgIndex > 1) {
            avatar = this.$http.post('/api/tag/upload/avatar', {
                avatar: this.avatar
            });
        }

        return Promise.all([tagPhoto, avatar]);
    }

    private getcanvas(
        url: string,
        text?: string,
        canvas: HTMLCanvasElement = document.createElement('canvas')
    ): Promise<HTMLCanvasElement> {
        const img = new Image();
        img.src = url;

        return new Promise((resolve, reject) => {
            img.onload = () => {
                canvas.width = 100;
                canvas.height = text ? 100 : 70;
                const ctx = <CanvasRenderingContext2D>canvas.getContext('2d');

                if (text) {
                    if (ctx.font !== '20px Arial') {
                        ctx.font = '20px Arial';
                    }
                    if (ctx.strokeStyle !== 'blue') {
                        ctx.strokeStyle = 'blue';
                    }
                    if (ctx.textAlign !== 'center') {
                        ctx.textAlign = 'center';
                    }

                    ctx.fillText(text, 50, 80);
                }

                ctx.drawImage(img, 25, 10, 50, 50);
                resolve(canvas);
            };

            img.onerror = reject;
        });
    }
}
